import './App.css'
import "./style.css"
import { useState, useEffect } from 'react'
import {BrowserRouter as Router, Switch, Route, Redirect } from 'react-router-dom';
import { createBrowserHistory } from "history";
import Home from './Components/Home.jsx'
import Home1 from './Components/Home1.jsx'

export default function App(props) {
  
  const history = createBrowserHistory();

  return (
      <Router forceRefresh={false} basename={process.env.PUBLIC_URL} history={history}>
        <Switch>
          <Route exact path={process.env.PUBLIC_URL} ><Redirect to="*" /></Route>
          <Route exact path='/' component={()=>{return <Home1 />}}/>
          <Route exact path='*' component={()=>{return <p>Null</p> }}/>



        </Switch>
      </Router>
  )
}


